<script setup lang="ts">
import { computed } from 'vue';
import type { Project } from '../data/portfolio';

const props = defineProps<{
  project: Project;
}>();

// Get top 8 meaningful words from description
const topWords = computed(() => {
  const stopWords = new Set([
    'the', 'a', 'an', 'and', 'or', 'but', 'in', 'on', 'at', 'to', 'for', 'of', 'with', 'by',
    'this', 'that', 'these', 'those', 'is', 'are', 'was', 'were', 'be', 'been', 'being',
    'have', 'has', 'had', 'do', 'does', 'did', 'will', 'would', 'should', 'could', 'can',
    'from', '.', '-', ''
  ]);
  
  const words = (props.project.description+props.project.title)
    .toLowerCase()
    .replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, ' ')
    .split(/\s+/)
    .filter(word => word.length > 2 && !stopWords.has(word) && word !== '.')
    .reduce((acc, word) => {
      // Remove trailing 's' for basic plural handling
      const singular = word.endsWith('s') ? word.slice(0, -1) : word;
      const key = singular;
      acc[key] = (acc[key] || 0) + 1;
      return acc;
    }, {} as Record<string, number>);
  
  return Object.entries(words)
    .sort((a, b) => b[1] - a[1])
    .slice(0, 5)
    .map(([word]) => word);
});

// Pseudo-random number generator based on project ID
const getRandomNumber = (index: number, min: number, max: number) => {
  const hash = props.project.id.split('').reduce((acc, char, i) => acc + char.charCodeAt(0) * (i + 1), 0);
  const value = Math.sin(hash * (index + 1)) * 10000;
  return min + (value - Math.floor(value)) * (max - min);
};

// Pre-defined positions for balanced layout
const layoutPositions = [
  { x: 0, y: 0, scale: 1.1 },      // Center
  { x: -100, y: -80, scale: 0.9 }, // Top left
  { x: 100, y: -80, scale: 0.9 },  // Top right
  { x: -90, y: 80, scale: 0.85 },  // Bottom left
  { x: 90, y: 80, scale: 0.85 },   // Bottom right
];

// Generate unique positions for each word
const wordPositions = computed(() => {
  return topWords.value.map((word, i) => {
    const basePosition = layoutPositions[i];
    const jitter = 10; // Small random offset for natural feel
    
    return {
      x: basePosition.x + getRandomNumber(i * 2, -jitter, jitter),
      y: basePosition.y + getRandomNumber(i * 2 + 1, -jitter, jitter),
      rotation: getRandomNumber(i * 3, -8, 8),
      scale: basePosition.scale * getRandomNumber(i * 4, 0.95, 1.05)
    };
  });
});

// Design-forward color palette inspired by modern aesthetics
const pastelColor = computed(() => {
  const colors = [
    { bg: '#E2F1C5', text: '#1C1C1C', accent: '#B8D989' }, // Chartreuse & charcoal
    { bg: '#D0E6FF', text: '#1A1A1A', accent: '#99C7FF' }, // Ice blue & deep gray
    { bg: '#FFE8D6', text: '#262626', accent: '#FFB27A' }, // Warm sand & carbon
    { bg: '#E0F4F4', text: '#1E1E1E', accent: '#A3E4E4' }, // Mint frost & onyx
    { bg: '#F4E0ED', text: '#232323', accent: '#E5B3D3' }  // Dusty rose & slate
  ];
  const hash = props.project.id.split('').reduce((acc, char, i) => acc + char.charCodeAt(0) * (i + 1), 0);
  return colors[hash % colors.length];
});
</script>

<template>
  <article class="project-card group" role="article" :aria-labelledby="`project-title-${props.project.id}`">
    <!-- Media section -->
    <template v-if="props.project.media?.src">
      <component
        :is="props.project.media.type === 'video' ? 'video' : 'img'"
        :src="props.project.media.src"
        :alt="props.project.media.alt"
        class="w-full object-contain bg-[#f8f8f8]"
        :aria-label="props.project.media.alt"
        v-bind="props.project.media.type === 'video' ? { 
          autoplay: true, 
          muted: true, 
          loop: true, 
          'webkit-playsinline': true, 
          playsinline: true,
          controls: false,
          'aria-label': props.project.media.alt
        } : {}"
      />
    </template>
    <template v-else>
      <div class="w-full h-[320px]"
           :style="{ background: `linear-gradient(165deg, ${pastelColor.bg}00 0%, ${pastelColor.bg} 100%)` }"
           role="img"
           :aria-label="`Word cloud visualization for ${props.project.title}`">
        <!-- Word cloud container -->
        <div class="relative w-full h-full flex items-center justify-center overflow-hidden group">
          <!-- Decorative rings -->
          <div class="absolute w-[28rem] h-[28rem] rounded-full transition-all duration-700"
               :style="{ border: `1.5px solid ${pastelColor.accent}`, opacity: 0.3 }">
          </div>
          <div class="absolute w-[20rem] h-[20rem] rounded-full transition-all duration-700"
               :style="{ border: `1.5px solid ${pastelColor.accent}`, opacity: 0.2 }">
          </div>
          <!-- Words -->
          <div v-for="(word, i) in topWords" 
               :key="word"
               class="absolute font-light tracking-widest transition-all duration-700 hover:scale-110 font-bold"
               :class="{
                 'text-xl': i >= 3,
                 'text-2xl': i >= 2 && i < 3,
                 'text-3xl': i >= 1 && i < 2,
                 'text-4xl': i < 1
               }"
               :style="{ 
                 color: pastelColor.text,
                 opacity: 0.95 - (i * 0.1),
                 transform: `
                   translate(${wordPositions[i].x}px, ${wordPositions[i].y}px)
                   rotate(${wordPositions[i].rotation}deg)
                   scale(${wordPositions[i].scale})
                 `,
                 letterSpacing: i >= 2 ? '0.25em' : '0.15em'
               }"
               role="text"
               :aria-label="word">
            {{ word }}
          </div>
          <!-- Explanation of word cloud -->
          <div class="absolute bottom-2 text-center font-mono text-xs md:text-sm uppercase border-t border-stone-900 -pt-2 md:opacity-0 group-hover:opacity-50 transition-opacity duration-300"
               :style="{ color: pastelColor.text }"
               role="note"
               aria-live="polite">
            Top 5 most frequent words from project description
          </div>
        </div>
      </div>
    </template>

    <!-- Content section -->
    <div class="space-y-6 px-2 mt-8">
      <h2 class="font-mono text-3xl leading-tight tracking-tight" :id="`project-title-${props.project.id}`" tabindex="0">
        {{ props.project.title }}
      </h2>

      <div class="space-y-4 max-w-xl" role="region" aria-label="Project description">
        <p 
          v-for="(paragraph, index) in props.project.description.split('\n\n')" 
          :key="index"
          class="font-mono leading-relaxed text-stone-600"
          tabindex="0"
        >
          {{ paragraph }}
        </p>
      </div>

      <div v-if="props.project.technologies?.length" 
           class="flex flex-wrap gap-2 font-mono"
           role="list"
           aria-label="Technologies used">
        <span 
          v-for="tech in props.project.technologies" 
          :key="tech"
          class="px-2 py-1 bg-stone-100 text-stone-600"
          role="listitem"
          tabindex="0"
        >
          {{ tech }}
        </span>
      </div>

      <div v-if="Object.keys(props.project.links || {}).length" 
           class="flex gap-6 font-mono text-md pt-4"
           role="navigation"
           aria-label="Project links">
        <a 
          v-for="(url, type) in props.project.links" 
          :key="type"
          :href="url"
          target="_blank"
          rel="noopener noreferrer"
          class="group/link relative inline-flex items-center text-stone-900 hover:text-stone-600 transition-colors"
          :aria-label="`${type === 'demo' ? 'View' : 
                        type === 'github' ? 'View Code' :
                        type === 'paper' ? 'Read Paper' :
                        type === 'external' ? 'Learn More' : 
                        'Visit'} ${props.project.title}`"
        >
          <span class="relative">
            {{ 
              type === 'demo' ? 'View' : 
              type === 'github' ? 'View Code' :
              type === 'paper' ? 'Read Paper' :
              type === 'external' ? 'Learn More' : 
              'Visit'
            }}
            <span class="absolute -bottom-px left-0 w-full h-px bg-stone-900/30 origin-left scale-x-0 transition-transform group-hover/link:scale-x-100"></span>
          </span>
          <span class="ml-1 transition-transform group-hover/link:translate-x-1" aria-hidden="true"></span>
        </a>
      </div>
    </div>
  </article>
</template>

<style scoped>
.project-card {
  @apply relative bg-white rounded-sm;
}

/* Bauhaus-inspired geometric accent */
.project-card::before {
  content: '';
  @apply absolute -left-4 top-0 w-px h-full bg-stone-200;
}

/* Japanese-inspired hover effect */
.project-card:hover::before {
  @apply bg-stone-900 transition-colors duration-500;
}
</style> 